/* nav-item SKEWED */
.nav-item.skewed  {
    background-color: transparent;
   }
.nav-item.skewed > a {
 transform: skew(30deg);
    -webkit-transform: skew(30deg);
    -moz-transform: skew(30deg);
    -o-transform: skew(30deg);
    border-left: 1px solid white;
    background-color: rgba(0, 48, 73, 0.08);
    height: 29px;
    min-width: 60px;
}
span.non-skew{
    transform: skew(-30deg);
    -webkit-transform: skew(-30deg);
    -moz-transform: skew(-30deg);
    -o-transform: skew(-30deg);

    pointer-events: none; 
    background-color: transparent;
    height: fit-content;
}


/* Menu dropdown */
.dropdown{
    font-family: Arial, sans-serif;
    position: absolute;
    top: 100%;  
    left: 0;
    /* margin-top: 2px; */
    min-width: 150px;
    width: max-content;
    width: -webkit-max-content;
    width: -moz-max-content;
    white-space: nowrap;
    transform: translateY(-10%);
    transition: .2s;

    opacity: 0;
    pointer-events: none;
    font-size: 1rem;
    border: 2px solid #5f6062;

}

.dropdown ul{
    position: relative;
    z-index: 1;
}

.dropdown-item > a{
    display: flex;
    background-color: #fff;
    color: #00141F;
    font-weight: 700;
    padding: .5rem 1rem;
    /* font-size: .9rem; */
    align-items: center;
    /* justify-content: space-between; */
    height: 32px;
    transition: .1s;
    /* border-bottom: 1px solid#5f6062; */
}


.dropdown-item:hover > a{
    background-color: #6385db;
    /* color: #fff; */
}

/* .dropdown-item{
    position: relative;
} */

.dropdown.second{
    top: -2px;
    left: 100%;
    cursor: pointer;
    transform: translateY(0);
    transition: none;
    font-size: .95rem;
    /* font-style: italic; */
    line-height: 1.3;
    text-align: justify;
    text-justify: inter-word;
    width: 80%;
    min-width: 200px;
    min-height: calc(100% + 4px);
    background-color: #fff;
    /* border-left-width: 2px; */
    opacity: 0;
    transition: opacity 0.4s;
    transition-delay: 0.3s;
    white-space: normal;
    overflow-y: auto;
}

.dropdown.second>ul{
    height: 100%;
}

.dropdown-item.desc{
    height: max-content;
    color: #00141F;
    padding: .5rem 1rem;
}


.nav-item:hover > .dropdown, 
.dropdown-item:hover > .dropdown{
    transform: translate(0, 0);
    opacity: 1;
    pointer-events: auto;
}

.nav-item:hover > .dropdown.second, 
.dropdown-item:hover > .dropdown.second{
    pointer-events: auto;
    opacity: 1;
}



